.App {
  text-align: center;
  background-image: url("https://image.freepik.com/free-vector/boy-playing-batery-background_23-2148084722.jpg");
  padding: 20px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.needGrid {
  width: 330px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  border: 1px solid salmon;
}

.outer-drum-pad {
  width: 350px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  padding: 30px;
  background-color: #56c2a9;
}

.inner-drum-pad {
  width: 75px;
  height: 75px;
  padding: 10px;
  margin: 10px;
  /* background-color: #D4D5D7; */
  border-radius: 8px;
  -webkit-box-shadow: 13px 14px 26px -12px rgba(0,0,0,0.67);
  -moz-box-shadow: 13px 14px 26px -12px rgba(0,0,0,0.67);
  box-shadow: 13px 14px 26px -12px rgba(0,0,0,0.67);
}

.drumboard {
  background-color: #562923;
  color: white;
  width: 350px;
  margin: 30px auto;
  padding: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.inner-display {
  width: 150px;
  height: 50px;
  padding: 10px;
  margin: 10px auto;
  color: black;
  line-height: 50px;
  background-color: #D4D5D7;
  border-radius: 8px;
  -webkit-box-shadow: 13px 14px 26px -12px rgba(0,0,0,0.67);
  -moz-box-shadow: 13px 14px 26px -12px rgba(0,0,0,0.67);
  box-shadow: 13px 14px 26px -12px rgba(0,0,0,0.67);
}
